@import url(https://css.gg/arrow-up.css);
@import url(https://css.gg/arrow-down.css);
@font-face {font-family: "DS-Digital"; src: url("//db.onlinewebfonts.com/t/63f874d192fb3892d88d5e26f942b5e2.eot"); src: url("//db.onlinewebfonts.com/t/63f874d192fb3892d88d5e26f942b5e2.eot?#iefix") format("embedded-opentype"), url("//db.onlinewebfonts.com/t/63f874d192fb3892d88d5e26f942b5e2.woff2") format("woff2"), url("//db.onlinewebfonts.com/t/63f874d192fb3892d88d5e26f942b5e2.woff") format("woff"), url("//db.onlinewebfonts.com/t/63f874d192fb3892d88d5e26f942b5e2.ttf") format("truetype"), url("//db.onlinewebfonts.com/t/63f874d192fb3892d88d5e26f942b5e2.svg#DS-Digital") format("svg"); }

body {
  margin: 0;
  font-family: 'Nunito Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: absolute;
  width: 100%;
  height: 100%;
  background: #10002B;
}

#time-left{
  font-family: 'DS-Digital';
}

*{
  box-sizing: border-box;
}
.App {
  text-align: center;
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #E0AAFF;

}
#title{
  position: absolute;
  top: 20px;
  font-size: 50px;
}

.controls{
  display: flex;
  flex-direction: column;
  position: absolute;
  width:250px;
  height: 200px;
  left: calc(50% - 125px);
  opacity: 0;
  -webkit-animation: appear 2s;
          animation: appear 2s;
}

#session{
  top: calc(50% - 200px);
}

#break{
  padding-top: 50px;
  top: calc(50% + 160px);
}

  .time-control-wrapper{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }

  .circle-wrapper{
    border: solid;
    border-radius: 70px;
  }

#display-wrapper{
  border: solid;
  border-radius: 55px;
  position: absolute;
  width: 300px;
  height: 150px;
  left: calc(50% - 150px);
  opacity: 0%;
  -webkit-animation: appear 2s;
          animation: appear 2s;
}

#display-wrapper button{
  outline: none;
  border: solid 1px;
  margin-left: 5px;
  background: none;
  color: #E0AAFF;
}
  #timer-label{
    margin-bottom: -30px;
  }

  #time-left{
    font-size: 45px;
  }
  
#control-wrapper{
  position: absolute;
  bottom: 12px;
  width: 50%;
  left: 25%;
}
  #break-label{
    margin-bottom: 0px;
  }
  #session-label{
    margin-bottom: 0px;
  }

  .gg-arrow-up{
    --ggs: 2;
    margin-top: 22px;
  }

  .gg-arrow-down{
    --ggs: 2;
    margin-top: 26px;
  }

  @-webkit-keyframes appear{
    from{opacity: 0;}
    to{opacity: 100%;}
  }

  @keyframes appear{
    from{opacity: 0;}
    to{opacity: 100%;}
  }

  
  @media only screen and (max-width: 500px){
    #title{
      font-size: 40px;
    }
    #display-wrapper{
      top: 25%;
    }
    #session{
      top: calc(25% + 160px);
    }

    #break{
      padding-top: 50px;
      top: calc(25% + 250px);
    }
  }
